/*----------------------------------------*/
/*  2.1 Header top area
/*----------------------------------------*/

.header-top-area {
    background: #E3E3E3;
}

.language-area ul>li {
    display: inline-block;
    margin-right: 22px;
    padding: 9px 0 11px;
    position: relative;
}

.account-area ul>li {
    display: inline-block;
    margin-right: 22px;
    padding: 8px 0 12px;
    position: relative;
}

.language-area ul>li:last-child,
.account-area ul>li:last-child {
    margin-right: 0px;
}

.language-area ul li img {
    margin-right: 8px;
}

.language-area ul li a,
.account-area ul li a {
    color: #333;
    font-size: 13px;
    text-decoration: none;
    text-transform: capitalize;
    font-family: 'Open Sans', sans-serif;
    transition: .3s;
}

.language-area ul li .header-sub {
    background: #fff none repeat scroll 0 0;
    box-shadow: 0 1px 7px -2px rgba(0, 0, 0, 0.3);
    left: 0;
    opacity: 0;
    padding: 15px 6px 18px 12px;
    position: absolute;
    text-align: left;
    top: 120%;
    transition: all 0.3s ease 0s;
    visibility: hidden;
    width: 100px;
    z-index: 234;
}

.language-area ul li .header-sub.dolor {
    width: 80px;
}

.language-area ul li:hover .header-sub {
    top: 100%;
    opacity: 1;
    visibility: visible;
}

.language-area ul li .header-sub ul li {
    display: block;
    margin: 0;
    padding: 0 0 10px;
}

.language-area ul li .header-sub ul li:last-child {
    padding: 0 0 0px;
}

.language-area ul li .header-sub ul li:last-child {
    border-bottom: 0;
}

.language-area ul li .header-sub ul li a {
    display: block;
}

.language-area ul li .header-sub ul li a img {
    margin: 0;
    padding-right: 8px;
}

.language-area ul li a i,
.account-area ul li a i {
    padding-left: 10px;
}

.account-area ul li {
    position: relative;
}

.account-area ul>li::before {
    background: #666 none repeat scroll 0 0;
    content: "";
    height: 12px;
    position: absolute;
    right: -13px;
    top: 50%;
    width: 1px;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
}

.account-area ul li:last-child:before {
    display: none;
}

.account-area ul li a:hover,
.language-area ul li a:hover {
    color: #f07c29;
}

.account-area ul li .header-sub ul li::before {
    display: none;
}

/*----------------------------------------*/
/*  2.2 Header mid area
/*----------------------------------------*/

.header-search {
    margin-top: 7px;
}

.header-search form {
    position: relative;
}

.header-search form input {
    background: #ededed none repeat scroll 0 0;
    border: 0 none;
    color: #333;
    font-size: 14px;
    height: 42px;
    line-height: 40px;
    margin: 0;
    padding-left: 10px;
    width: 100%;
    border-radius: 5px;
    padding-right: 47px;
}

.header-search form a {
    background: #f07c29 none repeat scroll 0 0;
    color: #fff;
    display: inline-block;
    font-size: 18px;
    height: 42px;
    line-height: 40px;
    position: absolute;
    right: 0;
    text-align: center;
    top: 0;
    width: 42px;
    border-radius: 0 5px 5px 0px;
}

.header-search form a:hover {
    background: #232323 none repeat scroll 0 0;
}

.header-mid-area .mini-cart {
    float: left;
    display: inline-block;
}

.header-mid-area .mini-cart ul li a {
    color: #666666;
    display: inline-block;
    font-size: 14px;
    line-height: 25px;
    text-transform: capitalize;
    transition: .3s;
    text-decoration: none;
}

.header-mid-area .mini-cart ul li a i {
    padding-right: 8px;
}

.header-mid-area .mini-cart ul li a:hover {
    color: #f07c29;
}

.header-mid-area .my-cart {
    display: inline-block;
    float: right;
    margin-top: -8px;
}

.header-mid-area .my-cart ul li {
    position: relative;
}

.header-mid-area .my-cart ul>li>a {
    color: #444444;
    display: inline-block;
    font-size: 16px;
    font-weight: 700;
    line-height: 30px;
    padding: 10px 0;
    text-transform: uppercase;
    text-decoration: none;
    transition: .3s;
}

.header-mid-area .my-cart ul>li:hover a {
    color: #f07c29;
}

.header-mid-area .my-cart ul li>a>i {
    font-size: 40px;
    padding-right: 30px;
}

.header-mid-area .my-cart ul li>span {
    background: #f07c29 none repeat scroll 0 0;
    border-radius: 50%;
    bottom: 0;
    color: #fff;
    display: inline-block;
    font-size: 18px;
    height: 30px;
    left: 24px;
    line-height: 30px;
    position: absolute;
    text-align: center;
    width: 30px;
}

.mini-cart-sub {
    position: absolute;
    background-color: #fff;
    border: medium none;
    border-radius: 0;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.15);
    display: block;
    left: auto;
    top: 150%;
    opacity: 0;
    padding: 20px;
    right: 0;
    transition: all 0.3s ease 0s;
    visibility: hidden;
    width: 280px;
    z-index: 99999;
}

.header-mid-area .my-cart ul>li:hover .mini-cart-sub {
    opacity: 1;
    visibility: visible;
    top: 110%;
}

.cart-product .single-cart {
    display: -webkit-flex;
    display: flex;
    margin-bottom: 15px;
    overflow: hidden;
}

.cart-product .single-cart .cart-img {
    -webkit-flex: 0 0 65px;
            flex: 0 0 65px;
    margin-right: 15px;
}

.cart-product .single-cart .cart-img a img {
    width: 100%;
}

.cart-product .single-cart .cart-info {
    overflow: hidden;
    padding-right: 10px;
    position: relative;
}

.cart-icon {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-grow: 100;
            flex-grow: 100;
    -webkit-justify-content: flex-end;
            justify-content: flex-end;
}

.cart-icon a i {
    color: #232323;
    transition: .3s;
}

.cart-icon a:hover i {
    color: #f07c29;
}

.cart-product .single-cart .cart-info h5 {
    margin-bottom: 0px;
}

.header-mid-area .my-cart ul>li .cart-product .single-cart .cart-info h5 a {
    color: #666;
    font-family: Rufina;
    font-size: 16px;
    font-weight: 400;
    line-height: 17px;
    margin-bottom: 0;
    text-transform: capitalize;
}

.header-mid-area .my-cart ul>li .cart-product .single-cart .cart-info h5 a:hover,
.header-mid-area .my-cart ul>li .cart-bottom a:hover {
    color: #f07c29;
}

.cart-product .single-cart .cart-info p {
    margin-bottom: 0;
    font-family: Rufina;
    font-size: 15px;
}

.cart-totals {
    margin-bottom: 15px;
}

.cart-totals h5 {
    color: #606060;
    display: block;
    font-size: 15px;
    font-weight: 400;
    font-family: Rufina;
    margin: 0;
    text-transform: uppercase;
}

.cart-totals h5 span {
    display: block;
    float: right;
}

.home-3 .header-mid-area .my-cart ul>li .mini-cart-sub .cart-totals h5 span {
